<script>
import dp from '@/assets/images/hisham.jpg'
export default {
  name: 'TopBar',
  props: {
    msg: String,
  },
  data() {
    return {
      dp: dp,
      name: 'SAYED HISHAM SAYYED JAFAR VALIYA MALIYAKKAL',
      ppn: 'V4597776',
      uid: '70081510',
      dob: new Date(1995, 6, 7),
      dobString: '',
      ppnString: '',
      uidString: '',
    }
  },
  mounted() {
    var dobMonth = this.dob.toLocaleString('default', { month: 'short' })
    this.dobString =
      ('0' + this.dob.getDate()).slice(-2) +
      ' ' +
      //   ('0' + (this.dob.getMonth() + 1)).slice(-2) +
      dobMonth +
      ' ' +
      this.dob.getFullYear()
    this.ppnString = this.ppn.substring(0, 4) + '****'
    this.uidString = this.uid.substring(0, 4) + '****'
    this.dobString = '***********'
    console.log(this.ppnString)
  },
}
</script>

